	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>Product Management Special Interest Group</h1>\n<p>Focuses on aspects of product management, such as the qualification and successful management of user requests, and aspects of project and program management such as the continued improvement of the processes used by the Kubernetes community to maintain the Kubernetes Project itself.\nBesides helping to discover both what to build and how to build it, the PM Group also helps to try and keep the wheels on this spaceship we are all building together; bringing together people who think about Kubernetes as both a vibrant community of humans and technical program is another primary focus of this group.\nMembers of the Kubernetes PM Group can assume certain additional responsibilities to help maintain the Kubernetes Project itself.\nIt is also important to remember that the role of managing an open source project is very new and largely unscoped for a project as large as Kubernetes; we are learning too and we are excited to learn how we can best serve the community of users and contributors.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/j/845373595\">Tuesdays at 16:00 UTC</a> (biweekly). <a href=\"http://www.thetimezoneconverter.com/?t=16:00&#x26;tz=UTC\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/1YqIpyjz4mV1jjvzhLx9JYy8LAduedzaoBMjpUKGUJQo/edit?usp=sharing\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/watch?v=VcdjaZAol2I&#x26;list=PL69nYSiGNLP3EBqpUGVsK1sMgUZVomfEQ\">Meeting recordings</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Aparna Sinha (<strong><a href=\"https://github.com/apsinha\">@apsinha</a></strong>), Google</li>\n<li>Ihor Dvoretskyi (<strong><a href=\"https://github.com/idvoretskyi\">@idvoretskyi</a></strong>), CNCF</li>\n<li>Caleb Miles (<strong><a href=\"https://github.com/calebamiles\">@calebamiles</a></strong>), Google</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/kubernetes-pm\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-pm\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Fnone\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>Subprojects</h2>\n<p>The following subprojects are owned by sig-none:</p>\n<ul>\n<li>\n<p><strong>features</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/features/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/features/master/OWNERS</a></li>\n</ul>\n</li>\n</ul>\n<!-- BEGIN CUSTOM CONTENT -->\n<h2>Common activities</h2>\n<ul>\n<li>Collecting and generalizing user feedback to help drive project direction and priorities: delivering on user needs while enforcing vendor neutrality</li>\n<li>Supporting collaboration across the community by working to improve the communication of roadmap and workload of other <a href=\"https://github.com/kubernetes/community#special-interest-groups-sig-and-working-groups\">Special Interest Groups</a></li>\n<li>Supporting the continued effort to improve the stability and extensibility of Kubernetes Project</li>\n<li>Supporting the marketing and promotion of the Kubernetes Project through the <a href=\"https://www.cncf.io/\">CNCF</a></li>\n<li>Working with the <a href=\"https://github.com/kubernetes/community/tree/master/contributors/devel/release\">Kubernetes Release Team</a> to continually ensure a high quality release of the Kubernetes Project</li>\n<li>Supporting the Kubernetes ecosystem through <a href=\"https://github.com/kubernetes/community/blob/master/incubator.md\">the Kubernetes Incubator</a></li>\n<li>Coordinating project wide policy changes for Kubernetes and the Kubernetes Incubator</li>\n<li>Onboarding large groups of corporate contributors and welcoming them into the Kubernetes Community</li>\n<li>\n<p>Whatever is needed to help make the project go!</p>\n<!-- END CUSTOM CONTENT -->\n</li>\n</ul>"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-product-management/"}}

